import PropTypes from 'prop-types';
import useMediaQuery from '@mui/material/useMediaQuery';
import Box from '@mui/material/Box';
import { Branding, BrandLogo } from 'components/brand';

import LoginButton from 'components/LoginButton';
import commonUtils from 'utils';
import { MyMemoriesButton } from 'components/Actions';

export default function Header({ authURL /* , next */ }) {
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';
  const belowmd = useMediaQuery((theme) => theme.breakpoints.down('md'));
  const action =
    isLoggedIn === true ? (
      <MyMemoriesButton />
    ) : (
      <LoginButton authURL={authURL} /* next={next} */ />
    );

  const brand =
    isLoggedIn === true && belowmd ? (
      <BrandLogo width="64px" height="64px" />
    ) : (
      <Branding />
    );

  return (
    <Box
      component="div"
      className="w-full px-4 py-3 mx-auto lg:py-3 md:px-24 lg:px-8"
      sx={{
        backgroundColor: 'primary.main',
        borderBottom: '3px solid #ffc745',
      }}
    >
      {/* eslint-disable-next-line max-len */}
      <div className="relative flex items-center justify-between max-w-6xl mx-auto lg:justify-between lg:space-x-16">
        {brand}
        <ul className="flex items-center space-x-8 lg:flex">{action}</ul>
      </div>
    </Box>
  );
}

Header.propTypes = {
  authURL: PropTypes.string.isRequired,
  // next: PropTypes.string.isRequired,
};
