import PropTypes from 'prop-types';
import Stack from '@mui/material/Stack';
import useMediaQuery from '@mui/material/useMediaQuery';
import Typography from '@mui/material/Typography';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import Divider from '@mui/material/Divider';
import Box from '@mui/material/Box';
import FiberManualRecordIcon from '@mui/icons-material/FiberManualRecord';

function FeatureTitle({ title }) {
  return <p className="text-xl font-bold text-gray-900">{title}</p>;
}

FeatureTitle.propTypes = {
  title: PropTypes.string.isRequired,
};

function FeatureDescription({ children }) {
  return (
    <Typography
      sx={{
        color: 'muted.text',
        fontWeight: 400,
        width: { xs: 'calc(90%)', sm: 'calc(60%)', lg: 'calc(100%)' },
        height: { lg: '15vh' },
      }}
    >
      {children}
    </Typography>
  );
}

FeatureDescription.propTypes = {
  children: PropTypes.node.isRequired,
};

function Feature({ title, next, children }) {
  const belowlg = useMediaQuery((theme) => theme.breakpoints.down('lg'));
  return belowlg ? (
    <Stack
      direction="column"
      justifyContent="flex-start"
      alignItems="center"
      spacing={2}
    >
      <FeatureTitle title={title} />
      <Divider sx={{ width: { xs: 'calc(90%)', sm: 'calc(60%)' } }} />
      <FeatureDescription>{children}</FeatureDescription>
      <Divider sx={{ width: { xs: 'calc(90%)', sm: 'calc(60%)' } }} />
      {next === true ? (
        <ArrowDownwardIcon color="primary" fontSize="large" />
      ) : null}
    </Stack>
  ) : (
    <Stack
      direction="row"
      justifyContent="center"
      alignItems="flex-start"
      spacing={2}
    >
      <Stack
        direction="column"
        justifyContent="space-between"
        alignItems="center"
        spacing={2}
      >
        <FeatureTitle title={title} />
        <Divider sx={{ width: '100%' }} />
        <FeatureDescription>{children}</FeatureDescription>
        <Divider sx={{ width: '100%' }} />
      </Stack>
      {next === true ? <ArrowForwardIcon color="primary" /> : null}
    </Stack>
  );
}

Feature.propTypes = {
  title: PropTypes.string.isRequired,
  next: PropTypes.bool.isRequired,
  children: PropTypes.node.isRequired,
};

export default function Features() {
  return (
    <div className="px-4 py-16 mx-auto w-full bg-white md:px-24 lg:px-8 lg:py-20">
      <Box
        component="div"
        className="max-w-xl mb-10 md:mx-auto sm:text-center lg:max-w-2xl md:mb-12"
        sx={{ textAlign: 'center' }}
      >
        <h2 className="max-w-lg mb-6 font-sans text-3xl font-bold leading-none tracking-tight text-gray-900 sm:text-4xl md:mx-auto">
          myMemoryStore.
        </h2>
        <Typography
          variant="h6"
          sx={{ fontWeight: 500, color: 'primary.main' }}
        >
          Never lose a precious memory again.
        </Typography>
        <Typography variant="h6">
          Capture <FiberManualRecordIcon sx={{ fontSize: '1rem' }} />{' '}
          Personalize <FiberManualRecordIcon sx={{ fontSize: '1rem' }} /> Share{' '}
          <FiberManualRecordIcon sx={{ fontSize: '1rem' }} /> Relive
        </Typography>
      </Box>
      <div className="mx-auto max-w-6xl grid gap-10 lg:grid-cols-3 sm:grid-cols-1">
        <Feature title="Upload" next>
          Upload your memory images. Ones that define you. Ones that will be as
          precious 20 years down the line.
        </Feature>
        <Feature title="Personalize" next>
          Once uploaded, add captions and stories that will be saved forever
          along with the photos themselves so they are not just another set of
          pixels on a screen but something more tangible and personal.
        </Feature>
        <Feature title="Share / Relive" next={false}>
          Share it with other people or don&apos;t. Just sit back and relive
          your memories whenever.
        </Feature>
      </div>
    </div>
  );
}
