import PropTypes from 'prop-types';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { alpha } from '@mui/material/styles';
import commonUtils from 'utils';
import LoginButton from 'components/LoginButton';
import { TopBarDialog } from './topbar/utils';
import About from './about';
import Contact from './contact';

function AppBarLink({ text, component, wdth }) {
  const [open, setOpen] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <>
      <Button onClick={handleOpen} sx={{ color: 'button.main' }}>
        <Typography variant="h6">{text}</Typography>
      </Button>
      <TopBarDialog
        text={text}
        wdth={wdth}
        open={open}
        onClose={handleClose}
        onClick={handleClose}
        sx={{ background: alpha('#000', 0.4) }}
      >
        {component}
      </TopBarDialog>
    </>
  );
}

function AppBarStack({ justify, children }) {
  return (
    <Stack
      direction="row"
      justifyContent={justify}
      alignItems="center"
      spacing={2}
      sx={{
        background: alpha('#000', 0.4),
        width: '100vw',
        position: 'fixed',
        height: '80px',
        top: 0,
        borderRight: '20px solid transparent',
        boxSizing: 'border-box',
      }}
    >
      {children}
    </Stack>
  );
}

export function MyMemoriesButton() {
  return (
    <Button
      type="submit"
      variant="contained"
      sx={{
        borderRadius: '5px',
        backgroundColor: 'white',
        '&:hover': {
          backgroundColor: 'form.input',
        },
      }}
    >
      <Link style={{ textDecoration: 'none' }} to="/memories">
        <Typography color="primary">My Memories</Typography>
      </Link>
    </Button>
  );
}

export default function Actions({ authURL, next }) {
  const cookieValue = commonUtils.getUserInfo();
  const isLoggedIn = cookieValue !== '';
  if (isLoggedIn === true) {
    return (
      <AppBarStack justify="flex-end">
        <AppBarLink text="About" wdth="lg" component={<About />} />
        <AppBarLink text="Contact" wdth="sm" component={<Contact />} />
        <MyMemoriesButton />
      </AppBarStack>
    );
  }
  return (
    <AppBarStack justify="flex-end">
      <AppBarLink text="About" wdth="lg" component={<About />} />
      <AppBarLink text="Contact" wdth="sm" component={<Contact />} />
      <LoginButton authURL={authURL} next={next} />
    </AppBarStack>
  );
}

Actions.propTypes = {
  authURL: PropTypes.string.isRequired,
  next: PropTypes.string.isRequired,
};

AppBarLink.propTypes = {
  text: PropTypes.string.isRequired,
  component: PropTypes.node.isRequired,
  wdth: PropTypes.string.isRequired,
};

AppBarStack.propTypes = {
  justify: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};
