import Button from '@mui/material/Button';
// import Typography from '@mui/material/Typography';
import PropTypes from 'prop-types';
import { alpha } from '@mui/material/styles';

export default function LoginButton({ authURL, next, handlePreLogin }) {
  const handleLogin = async () => {
    await handlePreLogin();
    console.log('pre logn handdled', next, authURL);
    const url =
      next !== ''
        ? `https://${authURL}/oauth2/authorize?next=${next}`
        : `https://${authURL}/oauth2/authorize`;
    window.location.replace(url);
  };

  return (
    <Button
      type="submit"
      variant="outlined"
      onClick={handleLogin}
      sx={{
        borderRadius: '5px',
        borderColor: 'white',
        fontWeight: 'bold',
        color: 'white',
        '&:hover': {
          color: 'white',
          borderColor: 'white',
          backgroundColor: alpha('#fff', 0.1),
        },
      }}
    >
      Login
    </Button>
  );
}

LoginButton.defaultProps = {
  handlePreLogin: () => {},
};

LoginButton.propTypes = {
  authURL: PropTypes.string.isRequired,
  next: PropTypes.string.isRequired,
  handlePreLogin: PropTypes.func,
};
